import uniIcons from '@/components/uni-icons/uni-icons.vue'
export default {
	components: {
		uniIcons
		
	},
	data() {
		return {
			imageList:[],
			name:"",
			idCard:"",
			phone:"",
			 array: ['请选择', '男', '女'],
			   sexIndex: 0,
			   workList:['请选择','政府','国家电网','财务','路政','纪委'],
			   workIndex:0,
		};
	},
	onShow(){
	uni.hideHomeButton()
	},
	methods:{
		jumpRegster(){
			uni.redirectTo({
			    url: '../login/login',
				
			});
		},
			
		submitMsg(){
			uni.showLoading({
			    title: '数据请求中'
			});
			uni.redirectTo({
			    url: '../regres/regres',
				
			});
		},
		//上传头像
		chooseImage(){
			uni.chooseImage({
				count:1,
				success: (res) => {
					 console.log(JSON.stringify(res.tempFilePaths));
					this.imageList = this.imageList.concat(res.tempFilePaths);
				},
				
			})
		},
		//选择单位
		workListChange(e){
			this.workIndex = parseInt(e.detail.value)
		},
			
		sizeTypeChange(e){
			this.sexIndex = parseInt(e.detail.value)
		}
	}
}
